.main {
  position: relative;
  width: 100vw;
  height: 100vh;
  top: 0;
  background-color: #FFFFFA;
  padding-top: 20px;
  font-weight:bold;
  overflow-x: hidden;
}

.circle{
  position: absolute;
  top: -100px;
  right:-100px;
}

.main h1 {
  font-size: 34px;
  padding:30px 0px 40px 100px;
  margin:0px;
  position: absolute;
  z-index:4;
}

.domain , .localisation , .period {
  margin:0px 20px 40px 20px;
}

.main h1 mark{
  background-color: #F9C80E;
  padding: 0px 5px 0px 5px;
}

.home-content {
  position:absolute;
  display: flex;
  width: 100%;
  flex-direction: column;
}

.home-counters {
  display: flex;
  width: 100%;
}

.top-in-row {
  display: flex;
  justify-content: space-around;
}

.main .content{
  display: flex;
  width: 100%;
  font-weight:normal;
  font-size:30px;
}

.main .image_home{
  margin-left:100px;
  width:50%;
}

.main .image_home img{
  width:600px;
}

.main .text_home{
  width:50%;
}

.main .text_home p:first-child{
  margin-top: 150px;
}

p mark{
  background-color: #F9C80E;
  padding: 0px 2px 0px 2px;
}

@media (min-width: 250px) and (max-width: 780px){
  .main {
    position: absolute;
    width: 100vw;
    top: 0;
    left: 0vw;
    padding-top: 80px;
    overflow-x: visible;
  }
  
  .circle{
    position: absolute;
    top: -150px;
    right:-150px;
  }

  .main h1 {
    margin-top:10px;
    padding:20px;
  }

  .main .image_home{
    margin-left:0px;
  }

  .main .text_home{
    width:100%;
    position: absolute;
    margin-top: 200px;
    text-align: center;
  }

  .main .text_home p:first-child{
    margin-top: 0px;
  }

  .domain{
    margin-top:20px;
    margin-left:20px;
  }

  .localisation{
    margin-top:20px;
    margin-left:20px;
  }

  .period{
    margin-top:20px;
    margin-left:20px;
  }

  .home-counters {
    display: block;
  }
}
